.node {
   width: 25px;
   height: 25px;
   outline: 1px solid rgb(175, 216, 248);
   display: inline-block;
 }
/*End Node Color..defined in Node.jsx*/
 .node-finish {
   background-color: red;
 }
/*Start Node Color..defined in Node.jsx*/
.node-start {
   background-color: green;
 }
 

.node-visited {
  animation-name: visitedNode;
  animation-duration: 1.4s;
  animation-timing-function: ease-in-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
/*keyframes specifies the animation code, it is created by gradually changing on set of css style to another...Style changes according to percent*/

@keyframes visitedNode {
   0% {
     transform: scale(0.3);
     background-color: rgba(0, 0, 66, 0.75);
     border-radius: 100%;
   }
 
   50% {
     background-color: rgba(217, 17, 187, 0.75);
   }
 
   75% {
     transform: scale(1.2);
     background-color: rgba(34, 17, 217, 0.75);
   }
 
   100% {
     transform: scale(1);
     background-color: rgba(151, 247, 113, 0.75);
   }
 }
/*defined in Node.jsx*/
 .node-wall {
   background-color: rgb(12, 53, 71);
 }
 
/*Animation for PlottingPath*/
.node-shortest-path {
   animation-name: shortestPath;
   animation-duration: 1.5s;
   animation-timing-function: ease-out;
   animation-delay: 0;
   animation-direction: alternate;
   animation-iteration-count: 1;
   animation-fill-mode: forwards;
   animation-play-state: running;
   
 }

 @keyframes shortestPath {
   0% {
     transform: scale(0.6);
     background-color: rgb(255, 254, 106);
   }
 
   50% {
     transform: scale(1.2);
     background-color: rgb(255, 254, 106);
   }
 
   100% {
     transform: scale(1);
     background-color: rgb(255, 254, 106);
   }
 }

